.loader {
  text-align: center;
  text-align: center;
  color: #fff;
  padding: 0 48px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  line-height: 86px;
  display: inline-block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 15px;
  height: 15px;
  background: currentColor;
  animation: load .7s ease-in-out infinite alternate;
  display: block;
  position: absolute;
  top: 0;
}

.loader:after {
  top: auto;
  bottom: 0;
}

@keyframes load {
  0% {
    height: 43px;
    width: 15px;
    left: 0;
    transform: translateX(0);
  }

  50% {
    height: 10px;
    width: 40px;
  }

  100% {
    height: 43px;
    width: 15px;
    left: 100%;
    transform: translateX(-100%);
  }
}

